
body {
      font-family: "PingFang SC", "Hiragino Sans GB", "Helvetica Neue", Roboto, Noto, sans-serif;
      font-size: 12px;
      -webkit-font-smoothing: antialiased;
      text-justify: 100%;
}
* {
      margin: 0;
      padding: 0;
      outline: 0;
      -webkit-user-select: none;
         -moz-user-select: none;
          -ms-user-select: none;
              user-select: none;
      -webkit-tap-highlight-color: transparent;
}
.head {
      width: 100%;
      line-height: 50px;
      text-align: center;
      background: #58a;
      color: #fff;
      margin-bottom: 20px;
      box-shadow: 0 1px 5px #888;
}
.descp {
      font-size: 14px;
      color: #333;
      margin-bottom: 20px;
      background: #f0f0f0;
      padding: 8px 10px;
      line-height: 1.5;
}
.divide {
      display: block;
      width: 8px;
      height: 8px;
      border-radius: 50%;
      background: #ccc;
      position: relative;
      margin: 40px auto;
}
.divide::before {
      content: '';
      position: absolute;
      left: -145px;
      left: -45vw;
      top: 4px;
      display: inline-block;
      width: 145px;
      width: 45vw;
      height: 1px;
      background: linear-gradient( 90deg, #eee, #ccc 40%, #aaa);
}
.divide::after {
      content: '';
      position: absolute;
      left: 8px;
      top: 4px;
      display: inline-block;
      width: 145px;
      width: 45vw;
      height: 1px;
      background: linear-gradient( 90deg, #aaa, #ccc 60%, #eee);
}



